.singleapp {
  .app-hero {
    height: 100vh;
    background: $warm-black;
    display: flex;
    align-items: center;
    &.light_theme {
      .hero-content {
        color: $warm-black;
      }
      .info-panel {
        li,
        a {
          color: $warm-black !important;
        }
      }
    }

    .button {
      margin-bottom: 10px;
    }

    @media #{$break-sm} {
      height: auto;
      padding: 15vw 0vw 15vw 0vw;
    }

    .grad {
      position: absolute;
      bottom: 0;
      left: 0;
      height: 100vh;
      width: 100vw;
      display: block;
      z-index: 10;
      background: -webkit-linear-gradient(
        rgba(16, 25, 32, 0.2),
        rgba(16, 25, 32, 1)
      );
      /* For Safari 5.1 to 6.0 */
      background: -o-linear-gradient(
        rgba(16, 25, 32, 0.2),
        rgba(16, 25, 32, 1)
      );
      /* For Opera 11.1 to 12.0 */
      background: -moz-linear-gradient(
        rgba(16, 25, 32, 0.2),
        rgba(16, 25, 32, 1)
      );
      /* For Firefox 3.6 to 15 */
      background: linear-gradient(rgba(16, 25, 32, 0.2), rgba(16, 25, 32, 1));
      /* Standard syntax */
    }

    .hero-content {
      color: #fff;
      width: 80vw;
      margin-left: 10vw;
      position: relative;
      z-index: 100;

      .info-panel {
        margin-top: 30px;
        width: 100%;
        border-bottom: 2px solid #fff;
        position: relative;

        @media #{$break-sm} {
          display: none;
        }

        .logo-small {
          position: absolute;
          display: block;
          width: 40px;
          height: auto;
          right: 0;
          top: 5px;
        }

        .breadcrumbs {
          padding-left: 0;

          .breadcrumb-arrow {
            width: 8px;
            height: auto;
            margin-top: -1px;
            margin: 0px 10px;
          }

          li {
            list-style: none;
            display: inline;
            text-transform: uppercase;
            font-size: 14px;

            @media #{$break-sm} {
              font-size: 11px;
              font-weight: 600;
            }

            a {
              font-size: inherit;
              color: #fff;

              &:hover {
                opacity: 0.7;
              }
            }
          }
        }
      }

      .app-info {
        margin-bottom: 10px;

        h1,
        p {
          display: inline;
          vertical-align: middle;
        }

        h1 {
          text-transform: uppercase;
          font-weight: 600;
          font-size: 50px;
          padding-right: 20px;
          border-right: 2px solid #fff;
          display: inline-block;
        }

        p {
          margin-left: 20px;
          font-family: "Crete Round", serif;
          font-style: italic;
          letter-spacing: 1px;
          font-size: 24px;
          width: 60vw;
          line-height: 30px;

          @media #{$break-sm} {
            width: 80vw;
          }
        }

        .coming-soon {
          padding: 5px;
          background: #fff;
          color: $warm-black;
          border-radius: 5px;
          display: inline-block;
        }

        &.long-title {
          h1,
          p {
            display: inline-block;
            margin: 10px 0px 0px 0px;
          }

          h1 {
            border-right: none;
            margin-bottom: 0px;
          }

          p {
            margin-left: 0px;
            margin-top: 10px;
          }
        }
      }

      .app-description {
        width: 50vw;
        margin-bottom: 20px;

        @media #{$break-sm} {
          width: 80vw;
        }
      }
    }
  }

  .small-nav {
    transform: translateY(0px);
    opacity: 1;
    position: fixed;
    z-index: 500;
  }

  .app-about {
    #glance {
      ul {
        background: $ultralight_gray;
        padding: 10px;
        border-radius: 10px;

        li {
          font-size: 18px;

          &::before {
            content: "";
            color: $yellow;
          }
        }
      }
    }

    @media #{$break-sm} {
      padding-top: 0px;
    }

    .awardlaurels {
      max-width: 100%;
      height: 20vh;

      padding-bottom: 20px;
    }

    p {
      margin-bottom: 28px;
    }

    h2 {
      text-transform: uppercase;
      font-size: 25px;
      padding-left: 0;
      margin-left: 0;
      padding-top: 90px;
      &:first-child {
        padding-top: 0px;
      }

      @media #{$break-sm} {
        margin-top: 20px;
      }
    }

    .standards-block {
      background: $ultralight_gray;
      padding: 2vw;
      margin: 2vw 0vw;
      border-radius: 5px;

      p {
        margin-bottom: 0px;
      }

      .bold {
        font-size: 20px;
      }

      .standards-explanation {
        font-style: italic;
        letter-spacing: 0px;
        border-left: 2px solid #fff;
        padding-left: 15px;
      }
    }

    ul {
      padding-left: 16px;
      list-style: none;
    }

    li {
      font-size: 15px;
    }

    .about-nav {
      @media #{$break-sm} {
        display: none;
      }

      ul {
        background: $ultralight_gray;
        display: inline-block;
        padding: 35px 20px 20px 30px;
        list-style: none;
        border-radius: 5px;

        li {
          margin-bottom: 20px;
          border-left: 4px solid #fff;
          padding-left: 10px;
          line-height: 25px;

          &:hover {
            border-left: 4px solid $light_red;
          }

          a {
            color: $warm-black;
            font-size: 16px;
            letter-spacing: 0px;
            text-transform: uppercase;
          }
        }
      }
    }

    .about-rightpanel {
      padding-left: 80px;

      @media #{$break-sm} {
        padding-left: 0px;
      }
    }
  }

  .scroll-locked {
    position: fixed;
    top: 100px;

    @media #{$break-sm} {
      position: relative;
      top: 0px;
    }
  }

  .app-intro {
    .intro-text {
      h2 {
        margin-left: 0px;
        text-transform: uppercase;
      }

      padding: 5vw 5vw 5vw 1vw;
    }
  }

  .play-button {
    height: 43px;
    width: auto;
    margin: 5px;
  }

