  /* Slider CSS
  ---------------------------*/

  .glide {
    $that: ".glide" !default;

    position: relative;
    width: 100%;
    height: 100%;

    &__wrapper {
      overflow: hidden;
    }

    &__track {
      position: relative;
      width: 100%;
      height: 100%;
      list-style: none;
      backface-visibility: hidden;
      transform-style: preserve-3d;
      touch-action: pan-Y;
      overflow: hidden;
      padding: 0;

      &.dragging {
        cursor: grabbing;
        user-select: none;
      }
    }

    &__slide {
      width: 100%;
      user-select: none;
      -webkit-touch-callout: none;
      -webkit-tap-highlight-color: transparent;

      a {
        user-select: none;
        -webkit-user-drag: none;
        -moz-user-select: none;
        -ms-user-select: none;
      }

      img {
        width: 100%;
        height: auto;
        margin: 0 auto;
      }
    }

    &__arrows {
      -webkit-touch-callout: none;
      user-select: none;
    }

    &__arrow {
      cursor: pointer;
    }

    &__bullets {
      -webkit-touch-callout: none;
      user-select: none;
    }

    &--slider {
      #{$that}__slide {
        float: left;
        clear: none;
      }

      #{$that}__arrow {
      }
    }

    &--carousel {
      &#{$that}--vertical {
        #{$that}__track {
          overflow: visible;
        }
      }

      #{$that}__slide {
        float: left;
        clear: none;
      }
    }

    &--slideshow {
      #{$that}__wrapper {
        height: 100%;
      }

      #{$that}__slide {
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        // Flashing issue fix
        -webkit-transform-style: preserve-3d;
        -webkit-transform: translate3d(0px, 0px, 0px);

        &.active {
          z-index: 1;
        }
      }
    }
  }

  .glide__bullets {
    text-align: center;
  }

  .glide__bullet {
    border-radius: 10px;
    width: 15px;
    height: 15px;
    outline: none;
    border-style: none;
    border: 2px solid #efefef;
    background-color: #fff;
    margin: 5px;

    &.active {
      background-color: #efefef;
    }
  }
}
