/* Styles of the Get Involved Page */

.get-involved {
	background: #fff;
	background-color: white;

	.page_hero {
		text-align: center;
		background-image: url(/assets/img/hero-images/getinvolved-bg.jpg);
		background-size: cover;
		background-position: right;
		height: 76vh;
		align-content: center;

		.container {
			padding: 4vw;
		    position: absolute;
		    display: block;
		    max-width: initial;
			left: 0; 
			right: 0; 
			margin-inline: auto; 
			width: fit-content;
			background: rgba(28, 29, 10, 0.95);
			border-radius: 20px;

			h1 {
				color: white;
				text-transform: uppercase;
				font-weight: 600;
				font-size: 4em;
				position: relative;
				display: inline-block;
				margin-bottom: 25px;
				z-index: 1;
				margin:0 auto;

				&:before {
					content: "";
					width: 100%;
					height: 17px;
					background: $warm-black;
					display: block;
					@media (max-width: 1130px) {
						display: none;
					}
					position: absolute;
					bottom: 5px;
					z-index: -1;
				}
			}
			p {
				color: #fff;
				font-size:18px;
				margin: 0 auto;
				width: 53vw;
				margin-top:10px;
			}
		}
	}

	

	#content-body {
		@media #{$break-sm} {
			display: block;
		}

		.row {
			padding-top: 5vw;
			margin-left: 6vw;
		}

		h2 {
			color: #011c25;
			font-family: brandon-grotesque, sans-serif;
			font-size: 24px;
			font-style: normal;
			font-weight: 450;
			line-height: normal;
			text-transform: uppercase;

			letter-spacing: 5.72px;
			margin-left: 0;
			margin-bottom: 10px;
			padding-top: 50px;
			&:first-child {
				margin-top: 0;
				padding-top: 0;
			}
		}

		p {
			margin-bottom: 10px;
		}

		.nav-wrap {
			@media #{$break-md} {
				display: none;
			}
		}

		.nav {
			ul {
				background: $ultralight_gray;
				display: inline-block;
				padding: 35px 20px 20px 30px;
				list-style: none;
				border-radius: 5px;
				li {
					color: $warm-black;
					margin-bottom: 20px;
					border-left: 4px solid #fff;
					padding-left: 10px;
					line-height: 25px;
					&:hover {
						border-left: 4px solid $light_red;
					}

					a {
						color: $warm-black;
						font-size: 16px;
						letter-spacing: 0px;
						text-transform: uppercase;
					}
				}
			}
		}

		.fact {
			padding: 40px 20px;
			height: fit-content;
			border-bottom: 3px solid $ultralight_gray;
			align-items: center;
			display: flex;
			gap: 1vw;

			@media #{$break-md} {
				flex-direction: column;

				&:nth-child(even) {
					flex-direction: column-reverse;
				}
			}

			.info-section {
				width: 75%;

				@media #{$break-md} {
					margin: 32px;
					width: 100%;
					gap: 3vw;
					// display: block;
				}
			}

			.img-section {
				width: 40%;

				@media #{$break-lg} {
					margin-right: 30px;
				}

				@media #{$break-md} {
					margin: 32px;
					width: 100%;
					// display: block;
				}

				img {
					display: flex;
					border-radius: 5px;
					width:100%;
					height:auto;
				}
			}
		}
	}

	// TODO modify footer with custom css
	.footer {
		// color: white;
		// background-color: #001e2a !important;
	}
}
